import React from 'react'
import {
    Box,
    Flex,
    HStack,
    Link,
    IconButton,
    Button,
    useDisclosure,
    useColorModeValue,
    Stack,
    useColorMode,
} from '@chakra-ui/react';
import { HamburgerIcon, CloseIcon, DownloadIcon, MoonIcon, SunIcon } from '@chakra-ui/icons';
import resume from '../../media/Meenakshi_Arora_Resume.pdf'

const Links = [
    {
        label: 'Home',
        class: "nav-link home",
        href: "#home"
    },
    {
        label: 'About',
        href: '#about',
        class: "nav-link about"
    },
    {
        label: 'Skills',
        class: "nav-link skills",
        href: "#skills"
    },
    {
        label: 'Projects',
        class: "nav-link projects",
        href: "#projects"
    },
    {
        label: 'Contact',
        class: "nav-link contact",
        href: "#contact"
    },
];

export default function Navbar() {
    const bgc = useColorModeValue('gray.200', 'gray.700')
    const { isOpen, onOpen, onClose } = useDisclosure();
    const { colorMode, toggleColorMode } = useColorMode();

    return (
        <>
            <Box bg={useColorModeValue('gray.100', 'gray.900')} px={4} position='fixed' top='0' w='100%' id="nav-menu">
                <Flex h={16} alignItems={'center'} justifyContent={'space-between'}>
                    <IconButton
                        size={'md'}
                        icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
                        aria-label={'Open Menu'}
                        display={{ md: 'none' }}
                        onClick={isOpen ? onClose : onOpen}
                    />
                    <HStack spacing={8} alignItems={'center'}>
                        <Box>Meenakshi</Box>
                        <HStack
                            as={'nav'}
                            spacing={4}
                            display={{ base: 'none', md: 'flex' }}>
                            {Links.map((e, i) => (
                                <Link px={2}
                                    py={1}
                                    rounded={'md'}
                                    _hover={{
                                        textDecoration: 'none',
                                        bg: bgc,
                                    }}
                                    fontWeight={'500'}
                                    key={i + 1}
                                    href={e.href ?? '#'}
                                    className={e.class}
                                >
                                    {e.label}
                                </Link>
                            ))}
                        </HStack>
                    </HStack>
                    <Flex alignItems={'center'}>
                        <a href={resume} download={resume} onClick={() => window.open('https://drive.google.com/file/d/1QTi0EefPcag_qGNduPRZ-20nLf96OOKU/view?usp=share_link')}>
                            <Button
                                variant={'solid'}
                                colorScheme={'teal'}
                                size={'sm'}
                                mr={4}
                                rightIcon={<DownloadIcon />}
                                // download={resume}
                                className="nav-link resume"
                            >
                                Resume
                            </Button>
                        </a>
                        <Button onClick={toggleColorMode}>
                            {colorMode === 'light' ? <MoonIcon /> : <SunIcon />}
                        </Button>
                    </Flex>
                </Flex>

                {isOpen ? (
                    <Box pb={4} display={{ md: 'none' }}>
                        <Stack as={'nav'} spacing={4}>
                            {Links.map((e, i) => (
                                <Link key={i + 1} href={e.href ?? '#'}>{e.label}</Link>
                            ))}
                        </Stack>
                    </Box>
                ) : null}
            </Box>

            {/* <Box p={4}>Main Content Here</Box> */}
        </>
    );
}