import { Box, Heading, Image, Text } from '@chakra-ui/react';
import pic from '../../media/Profilepicture.jpg';

const About = () => {
    return (
        <Box
            py='70px'
            w='100%'
            id="about"
            className="about section"
        >
            <Heading textAlign={'center'}>About me</Heading>
            <Box
                display={{ 'base': 'grid', 'md': 'flex', 'lg': 'flex' }}
                gap='20px'
                justifyContent='space-evenly'
                p='4%'
            >
                <Box
                // border={"1px solid"}
                >
                    <Image
                        src={pic}
                        w={{ 'base': '200px', 'md': '300px' }}
                        className   ="home-img"
                        borderRadius='30%'
                    />
                </Box>
                <Box
                    // border={"1px solid"}
                    w='40%'>
                    <Text
                        fontWeight={700}
                        fontSize='18px'
                        textAlign={'center'}
                        id="user-detail-intro"
                    >
                        A full stack web developer with a keen eye for detail and a passion for creating bug-free, visually appealing, and secure web applications.
                    </Text>
                </Box>
            </Box>
        </Box>
    )
}
export default About;